@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
body {
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
  margin: 0;
  min-height: 100vh;
  width: 100vw;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6, p { 
  font-weight: 400;
  margin: 0;
}
